*{
    margin:0;
    padding:0;
    box-sizing: border-box;
    font-family: 'Nunito', sans-serif;
}


:root{
    --background:#2B3539;
    --card-bg: #222B2F;
    --card-bg-hover: #20282c;
    --text-color: #A9B5D3;
    --dark-bg:#273236;

    --menu-bg:  rgba(34,45,50,1);
	--menu-item-text: rgba(186, 204, 238, 0.62);
	--current-section: #8AE133;
    --menu-item-text: rgba(186, 204, 238, 0.62);

    --web-color:#8C3BEF;
    --desk-color: #F19272;
}

.center-container{
    width:90%;
    margin:0 5%;
}

.mobile-only{
    display:block;
}

.desk-only{
    display:none;
}

@media (min-width: 768px){
    .desk-only{
        display: block;
    }
    .mobile-only{
        display: none;
    }
    .center-container{
        width:75%;
        margin:0 12.5%;
    }
}

@media( max-width: 1080px){
    html{
        font-size:93.75%;
    }
}
@media( max-width: 720px){
    html{
        font-size:87.5%;
    }
}
body{
    background: var(--background);
    overflow-x: hidden;
}

body, input, textarea, button{
    font:400 1rem 'Nunito', sans-serif;
}


button{
    cursor: pointer;
}

a{
    color: inherit;
    text-decoration: none;
    color:#09C;
}

h2{
    color:#94ABC0;
}

img, video, iframe{
    max-width:100%;
}

body, html, #root{
    width:100%;
    height:100%;
    position: relative;
}

li{
    list-style:none;
}

#wrapper{
    float: left;
    width:100%;
    height: 100%;
    position: relative;
}

.section-title{
    padding:5px 0 25px;
    width:100%;
    text-align: center;
}